.pipeCenterLayout{margin:20px 5% 40px}.pipeCenterLayout .containerDisplay{display:flex;margin-bottom:32px}.pipeCenterLayout .containerDisplay>:not(:first-child){margin-left:8px}.pipeCenterLayout .pipeCard{display:flex;flex-direction:column;min-width:220px;width:220px;height:170px;padding:8px 16px;background-color:#fff;border-radius:5px;box-shadow:2px 2px 8px hsla(0,0%,80.8%,.26);transition:transform .3s}.pipeCenterLayout .pipeCard .pipeDeleteIcon{position:relative;left:145px;height:0}.pipeCenterLayout .pipeCard .pipeDeleteIcon .favoriteIcon{color:#505f6f;font-size:22px}.pipeCenterLayout .pipeCard .pipeFavoriteIcon{position:relative;left:170px;height:0}.pipeCenterLayout .pipeCard .pipeFavoriteIcon .favoriteIcon{color:red;font-size:22px}.pipeCenterLayout .pipeCard .pipeLink{display:flex;flex-direction:column;flex-grow:2;align-items:center;width:100%}.pipeCenterLayout .pipeCard .pipeLink .pipeCount{display:flex;align-items:center;height:100%;font-weight:700;font-size:16px}.pipeCenterLayout .pipeCard .pipeLink .pipeCount .pipeCountLabel{margin-top:18px;margin-left:4px}.pipeCenterLayout .pipeCard .pipeLink .pipeName{color:#949ca0;font-size:14px;text-align:center}.pipeCenterLayout .pipeCard:hover{border:1px solid #1890ff;transform:translateY(-5px)}.formInput{display:flex;flex-direction:column}.formInput label{font-size:14px;font-weight:600;color:#505f6f;margin-top:4px;margin-bottom:4px}.formInput input{outline:none;border:1px solid #f5f7fb;padding:8px;border-radius:5px}.formInput input:focus{border:1px solid #0094c6}